import React, { useState, useContext } from 'react';
import { ArrowRight, Award, Globe, Sparkles, Send, RefreshCw } from 'lucide-react';

// Expect setFooterVisible and setCurrentSection to be passed as props or via context if you want to control App state from here.
// For this example, we'll use window events for communication.

const ProfessionalFooter = ({ setFooterVisible, setCurrentSection }) => {
  const [currentYear] = useState(new Date().getFullYear());
  const [hoveredLink, setHoveredLink] = useState(null);
  const [scrolled, setScrolled] = useState(false);

  const footerSections = [
    {
      title: 'Prototype Features',
      icon: <Award className="w-5 h-5" />,
      links: [
        { name: 'Raspberry Pi 5', href: '/#about', desc: 'Main processing unit' },
        { name: 'ESP32 LoRa', href: '/#about', desc: 'Long-range wireless communication' },
        { name: 'ESP32 PoE', href: '/#about', desc: 'Power over Ethernet for flexibility' },
        { name: 'Raspberry Pi Camera', href: '/#about', desc: 'Visual hazard detection' },
        { name: 'System Diagrams', href: '/#about', desc: 'See hardware and architecture images' }
      ]
    },
    {
      title: 'Resources',
      icon: <Globe className="w-5 h-5" />,
      links: [
        { 
          name: 'Technical Documentation', 
          href: '#', // Use '#' and handle click
          desc: 'Schematics and integration guides',
          onClick: (e) => {
            e.preventDefault();
            scrollToTop();
          }
        }
      ]
    },
    {
      title: 'About the Project',
      icon: <Sparkles className="w-5 h-5" />,
      links: [
        { name: 'Prototype Status', href: '/#about', desc: 'No clients yet, research phase' },
        { name: 'Hardware Gallery', href: '/#about', desc: 'Images of boards and wiring' }
        // Removed non-existing links
      ]
    }
  ];

  return (
    <div className="w-full min-h-screen relative bg-slate-900 px-2 sm:px-0">
      <div className="container mx-auto px-2 sm:px-6 py-10 sm:py-20">
        {/* Header Section */}
        <div className="text-center mb-10 sm:mb-20">
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-8">
            <div className="w-14 h-14 sm:w-16 sm:h-16 bg-gradient-to-br from-blue-500 via-cyan-400 to-purple-500 rounded-2xl flex items-center justify-center shadow-2xl">
              <span className="text-white font-bold text-xl sm:text-2xl">V</span>
            </div>
            <div>
              <div className="text-3xl sm:text-4xl font-bold text-white mb-2">
                VI<span className="bg-gradient-to-r from-blue-400 to-cyan-400 bg-clip-text text-transparent">PRA</span>
              </div>
              <div className="text-xs sm:text-sm text-slate-400 font-medium">Distributed Road Safety Prototype</div>
            </div>
          </div>

          <p className="text-base sm:text-xl text-slate-300 max-w-3xl mx-auto leading-relaxed">
            We are developing a distributed road safety prototype using Raspberry Pi 5, ESP32 LoRa, ESP32 PoE, and Pi Camera. The system is not instantaneous but is designed for low power and real-world feasibility. No clients yetthis is a research prototype.
          </p>
        </div>

        {/* Main Content */}
        <div className="flex-grow overflow-y-auto pb-8">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8 sm:gap-12 mb-8 flex-grow overflow-y-auto">
            {footerSections.map((section, sectionIndex) => (
              <div key={section.title} className="group">
                <div className="flex items-center gap-3 mb-8">
                  <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-500/20 to-purple-500/20 border border-white/10 flex items-center justify-center text-blue-400 group-hover:scale-110 transition-transform duration-300">
                    {section.icon}
                  </div>
                  <h3 className="text-lg font-bold text-white group-hover:text-blue-400 transition-colors duration-300">
                    {section.title}
                  </h3>
                </div>

                <ul className="space-y-4">
                  {section.links.map((link, linkIndex) => (
                    <li key={link.name}>
                      <a
                        href={link.href}
                        className="group/link block p-3 rounded-lg hover:bg-slate-800/30 transition-all duration-300"
                        onClick={link.onClick}
                        onMouseEnter={() => setHoveredLink(`${sectionIndex}-${linkIndex}`)}
                        onMouseLeave={() => setHoveredLink(null)}
                      >
                        <div className={`font-medium transition-colors duration-300 mb-1 ${hoveredLink === `${sectionIndex}-${linkIndex}`
                            ? 'text-blue-400'
                            : 'text-slate-300 group-hover/link:text-white'
                          }`}>
                          {link.name}
                        </div>
                        <div className="text-sm text-slate-500 group-hover/link:text-slate-400 transition-colors duration-300">
                          {link.desc}
                        </div>
                      </a>
                    </li>
                  ))}
                </ul>
              </div>
            ))}

            {/* Newsletter Section */}
            <div className="group">
              <div className="flex items-center gap-3 mb-8">
                <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-green-500/20 to-emerald-500/20 border border-white/10 flex items-center justify-center text-green-400 group-hover:scale-110 transition-transform duration-300">
                  <Send className="w-5 h-5" />
                </div>
                <h3 className="text-lg font-bold text-white group-hover:text-green-400 transition-colors duration-300">
                  Stay Updated
                </h3>
              </div>

              <p className="text-slate-300 mb-6 leading-relaxed">
                Receive exclusive insights, road safety trends, and project updates directly to your inbox.
              </p>

              <form className="space-y-4">
                <input
                  type="email"
                  placeholder="la-tua@email.com"
                  className="w-full bg-slate-800/50 border border-slate-700/50 rounded-xl px-4 py-3 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-green-500 focus:border-transparent transition-all duration-300"
                />

                <button
                  type="submit"
                  className="w-full group/btn relative overflow-hidden bg-gradient-to-r from-green-600 to-emerald-600 hover:from-green-700 hover:to-emerald-700 text-white px-6 py-3 rounded-xl font-semibold transition-all duration-300 transform hover:scale-[1.02] shadow-lg"
                >
                  <span className="absolute inset-0 bg-gradient-to-r from-white/10 to-transparent transform -skew-x-12 -translate-x-full group-hover/btn:translate-x-full transition-transform duration-1000" />
                  <div className="relative flex items-center justify-center gap-2">
                    <Send size={16} className="group-hover/btn:rotate-12 transition-transform duration-300" />
                    Iscriviti Ora
                  </div>
                </button>
              </form>
            </div>
          </div>
        </div>

        {/* Bottom Section */}
        <div className="pt-8 border-t border-slate-800">
          <div className="flex flex-col lg:flex-row justify-between items-center gap-4 sm:gap-8">
            <div className="flex flex-col lg:flex-row items-center gap-4 sm:gap-6 text-xs sm:text-sm text-slate-400">
              <p>&copy; {currentYear} VIPRA Project. All rights reserved.</p>
              <div className="flex items-center gap-4 sm:gap-6">
                <a href="/privacy-policy" className="hover:text-blue-400 transition-colors duration-300">Privacy Policy</a>
                <a href="/terms-and-conditions" className="hover:text-blue-400 transition-colors duration-300">Terms of Service</a>
                <a href="/cookie-policy" className="hover:text-blue-400 transition-colors duration-300">Cookie Policy</a>
              </div>
            </div>

            <div className="flex items-center gap-2 sm:gap-4">
              <span className="text-xs sm:text-sm text-slate-400">Follow us:</span>
              <div className="flex gap-2 sm:gap-3">
                {/* Only Instagram is real, others removed */}
                <a
                  href="https://instagram.com/vipra.prj"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="w-8 h-8 sm:w-10 sm:h-10 rounded-lg bg-slate-800/50 border border-slate-700/50 flex items-center justify-center text-slate-400 hover:text-white hover:bg-blue-500/20 hover:border-blue-500/30 hover:scale-110 transition-all duration-300 backdrop-blur-sm"
                >
                  <span className="sr-only">Instagram: @vipra.prj</span>
                  <div className="w-4 h-4 bg-current rounded-sm opacity-80" />
                </a>
              </div>
            </div>
          </div>
        </div>

        {/* Aggiungi questo elemento alla fine del footer */}
        <div className="text-center mt-20 pt-12 border-t border-slate-700">
          <p className="text-slate-400 mb-6">You have reached the end of the content</p>
          <button
            onClick={() => {
              window.scrollTo(0, 0);
              // Usa i callback se forniti, altrimenti dispatcha un evento custom
              if (setFooterVisible && setCurrentSection) {
                setTimeout(() => {
                  setFooterVisible(false);
                  setCurrentSection(0);
                }, 100);
              } else {
                // fallback: dispatch custom event for App.js to listen
                setTimeout(() => {
                  window.dispatchEvent(new CustomEvent('restartAppSections'));
                }, 100);
              }
            }}
            className="inline-flex items-center gap-2 text-blue-400 hover:text-white transition-colors"
          >
            <RefreshCw size={18} />
            Start Over
          </button>
        </div>
      </div>
    </div>
  );

function scrollToTop() {
  window.scrollTo(0, 0);
  // Usa i callback se forniti, altrimenti dispatcha un evento custom
  if (typeof setFooterVisible === 'function' && typeof setCurrentSection === 'function') {
    setTimeout(() => {
      setFooterVisible(false);
      setCurrentSection(0);
    }, 100);
  } else {
    setTimeout(() => {
      window.dispatchEvent(new CustomEvent('restartAppSections'));
    }, 100);
  }
}

};

export default ProfessionalFooter;