import React, { useState, useEffect } from 'react';
import { Menu, X, Globe, Zap, Eye, TrendingUp, Users, ArrowRight, Home } from 'lucide-react';
import logo from '../assets/LogoVIPRA.png';

const ProfessionalHeader = ({ currentSection, navigateToSection, sections }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const menuItems = [
    { name: 'Home', icon: <Home size={16} />, index: 0 },
    { name: 'Features', icon: <Zap size={16} />, index: 1 },
    { name: 'Stats', icon: <TrendingUp size={16} />, index: 2 },
    { name: 'About', icon: <Eye size={16} />, index: 3 },
    { name: 'Contact', icon: <Users size={16} />, index: 4 },
    { name: 'Documentation', icon: <Users size={16} />, index: 5 },
  ];

  const handleNavigation = (index) => {
    navigateToSection(index);
    setIsMenuOpen(false);
  };

  // Close menu when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (isMenuOpen && !event.target.closest('.mobile-menu') && !event.target.closest('.menu-button')) {
        setIsMenuOpen(false);
      }
    };

    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, [isMenuOpen]);

  return (
    <>
      <header className="fixed top-0 w-full z-50 bg-slate-900/95 backdrop-blur-lg border-b border-slate-700/50 shadow-xl pt-2 pb-2">
        <nav className="container mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            {/* Logo */}
            <div 
              className="flex items-center space-x-3 transform hover:scale-105 transition-transform duration-300 cursor-pointer"
              onClick={() => handleNavigation(0)}
            >
              <img
                src={logo}
                alt="Logo"
                className="w-10 h-10 object-contain drop-shadow-lg transition-all duration-300"
                style={{ filter: 'drop-shadow(0 0 8px #38bdf8)' }}
              />
              <div className="text-2xl font-bold text-white">
                V<span className="text-blue-400">I</span>PR<span className="text-blue-400">A</span>
              </div>
            </div>
            
            {/* Section Indicator */}
            <div className="hidden sm:flex items-center gap-2 bg-slate-800/50 backdrop-blur-sm px-4 py-2 rounded-lg border border-slate-700/50">
              <div className="w-2 h-2 bg-blue-400 rounded-full animate-pulse" />
              <span className="text-sm text-slate-300 font-medium">
                {menuItems[currentSection]?.name || 'Section'} ({currentSection + 1}/{sections.length})
              </span>
            </div>

            {/* Hamburger Menu Button */}
            <button
              className="menu-button relative w-12 h-12 bg-slate-800/50 backdrop-blur-sm rounded-lg border border-slate-700/50 hover:border-blue-500/50 transition-all duration-300 transform hover:scale-105 flex items-center justify-center group"
              onClick={() => setIsMenuOpen(!isMenuOpen)}
            >
              <div className="relative w-6 h-6">
                <span className={`absolute block w-6 h-0.5 bg-blue-400 transition-all duration-300 ${isMenuOpen ? 'rotate-45 top-3' : 'top-1'}`} />
                <span className={`absolute block w-6 h-0.5 bg-blue-400 top-3 transition-all duration-300 ${isMenuOpen ? 'opacity-0' : 'opacity-100'}`} />
                <span className={`absolute block w-6 h-0.5 bg-blue-400 transition-all duration-300 ${isMenuOpen ? '-rotate-45 top-3' : 'top-5'}`} />
              </div>
            </button>
          </div>
        </nav>
      </header>

      {/* Mobile Menu Overlay */}
      <div className={`mobile-menu fixed inset-0 z-40 transition-all duration-500 ${
        isMenuOpen ? 'opacity-100 visible' : 'opacity-0 invisible'
      }`}>
        <div 
          className="absolute inset-0 bg-black/60 backdrop-blur-sm"
          onClick={() => setIsMenuOpen(false)}
        />
        
        {/* Menu Panel */}
        <div className={`absolute top-0 right-0 h-full w-full max-w-sm bg-slate-900/95 backdrop-blur-xl border-l border-slate-700/50 shadow-2xl transform transition-transform duration-500 ${
          isMenuOpen ? 'translate-x-0' : 'translate-x-full'
        }`}>
          <div className="p-6 pt-24">
            {/* Current Section Highlight */}
            <div className="mb-8 p-4 bg-gradient-to-r from-blue-500/10 to-cyan-500/10 border border-blue-500/30 rounded-xl">
              <div className="flex items-center gap-3 text-blue-400 mb-2">
                {menuItems[currentSection]?.icon}
                <span className="font-semibold">Current Section</span>
              </div>
              <h3 className="text-white text-lg font-bold">{menuItems[currentSection]?.name}</h3>
              <div className="mt-2 w-full bg-slate-700 rounded-full h-1">
                <div 
                  className="bg-gradient-to-r from-blue-500 to-cyan-500 h-1 rounded-full transition-all duration-300"
                  style={{ width: `${((currentSection + 1) / sections.length) * 100}%` }}
                />
              </div>
            </div>

            {/* Navigation Menu */}
            <div className="space-y-3">
              <h4 className="text-slate-400 text-sm font-semibold uppercase tracking-wider mb-4">
                Navigazione
              </h4>
              
              {menuItems.map((item, index) => (
                <button
                  key={item.name}
                  onClick={() => handleNavigation(item.index)}
                  className={`w-full flex items-center justify-between p-4 rounded-xl transition-all duration-300 transform hover:scale-[1.02] ${
                    currentSection === item.index
                      ? 'bg-gradient-to-r from-blue-500/20 to-cyan-500/20 border border-blue-500/30 text-blue-400'
                      : 'text-slate-300 hover:text-white hover:bg-slate-800/50 border border-transparent hover:border-slate-700/50'
                  }`}
                  style={{ animationDelay: `${index * 0.1}s` }}
                >
                  <div className="flex items-center space-x-3">
                    <div className={`w-10 h-10 rounded-lg flex items-center justify-center transition-all duration-300 ${
                      currentSection === item.index
                        ? 'bg-gradient-to-br from-blue-500 to-cyan-500 text-white'
                        : 'bg-slate-800 text-slate-400 group-hover:text-blue-400'
                    }`}>
                      {item.icon}
                    </div>
                    <div className="text-left">
                      <span className="font-semibold block">{item.name}</span>
                      <span className="text-xs text-slate-500">Section {item.index + 1}</span>
                    </div>
                  </div>
                  
                  {currentSection === item.index && (
                    <div className="w-2 h-2 bg-blue-400 rounded-full animate-pulse" />
                  )}
                </button>
              ))}
            </div>

            {/* Quick Actions */}
            <div className="mt-8 pt-6 border-t border-slate-700/50">
              <button 
                onClick={() => handleNavigation(4)}
                className="w-full flex items-center justify-center gap-3 bg-gradient-to-r from-blue-600 to-cyan-600 hover:from-blue-700 hover:to-cyan-700 text-white px-6 py-4 rounded-xl font-semibold transition-all duration-300 transform hover:scale-105 shadow-lg group"
              >
                <span>Contact Us</span>
                <ArrowRight size={16} className="group-hover:translate-x-1 transition-transform duration-300" />
              </button>
            </div>

            {/* Progress Indicator */}
            <div className="mt-6 text-center">
              <div className="text-xs text-slate-500 mb-2">
                Progress: {currentSection + 1} di {sections.length}
              </div>
              <div className="flex justify-center space-x-1">
                {sections.map((_, index) => (
                  <button
                    key={index}
                    onClick={() => handleNavigation(index)}
                    className={`w-2 h-2 rounded-full transition-all duration-300 ${
                      currentSection === index
                        ? 'bg-blue-500 scale-125'
                        : 'bg-slate-600 hover:bg-slate-400'
                    }`}
                  />
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ProfessionalHeader;