import React, { useState, useEffect, useRef } from 'react';
import ProfessionalParticleSystem from './components/ProfessionalParticleSystem';
import ProfessionalLoader from './components/ProfessionalLoader';
import ProfessionalHeader from './components/ProfessionalHeader';
import ProfessionalHero from './components/ProfessionalHero';
import ProfessionalFeatures from './components/ProfessionalFeatures';
import ProfessionalStats from './components/ProfessionalStats';
import ProfessionalAbout from './components/ProfessionalAbout';
import ProfessionalContact from './components/ProfessionalContact';
import ProfessionalFooter from './components/ProfessionalFooter';
import ProfessionalRelazione from './components/ProfessionalRelazione';

const App = () => {
  const [isLoading, setIsLoading] = useState(true);
  const [currentSection, setCurrentSection] = useState(0);
  const [footerVisible, setFooterVisible] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth < 768);
  const mainContainerRef = useRef(null);
  const footerRef = useRef(null);
  const sectionRefs = useRef([]);

  const sections = [
    { id: 'home', component: <ProfessionalHero /> },
    { id: 'features', component: <ProfessionalFeatures /> },
    { id: 'stats', component: <ProfessionalStats /> },
    { id: 'about', component: <ProfessionalAbout /> },
    { id: 'contact', component: <ProfessionalContact /> },
    { id: 'relazione', component: <ProfessionalRelazione /> }
  ];

  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth < 768);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Scroll infinito solo su desktop
  useEffect(() => {
    if (isMobile) return;
    const handleScroll = (e) => {
      // Se siamo nel footer e scrolliamo ancora verso il basso
      if (footerVisible && window.scrollY + window.innerHeight >= document.documentElement.scrollHeight - 10 && e.deltaY > 0) {
        setFooterVisible(false);
        setCurrentSection(0);
        window.scrollTo(0, 0);
        return;
      }

      // Se siamo in una sezione con overflow
      const currentSectionEl = sectionRefs.current[currentSection];
      if (currentSectionEl) {
        const { scrollTop, scrollHeight, clientHeight } = currentSectionEl;
        const isAtBottom = scrollTop + clientHeight >= scrollHeight - 10;
        const isAtTop = scrollTop === 0;

        if (e.deltaY > 0 && isAtBottom) {
          // Scroll down alla fine della sezione
          if (currentSection < sections.length - 1) {
            setCurrentSection(prev => prev + 1);
          } else {
            setFooterVisible(true);
            setTimeout(() => {
              footerRef.current?.scrollIntoView({ behavior: 'smooth' });
            }, 50);
          }
        } else if (e.deltaY < 0 && isAtTop) {
          // Scroll up all'inizio della sezione
          if (currentSection > 0) {
            setCurrentSection(prev => prev - 1);
          }
        } else {
          // Permetti scroll verticale dentro la sezione
          return;
        }
      }

      e.preventDefault();
    };

    window.addEventListener('wheel', handleScroll, { passive: false });
    return () => window.removeEventListener('wheel', handleScroll);
  }, [currentSection, footerVisible, sections.length, isMobile]);

  useEffect(() => {
    if (isMobile) return;
    // All'avvio: vai alla sezione dell'hash se presente
    const hash = window.location.hash.replace('#', '');
    if (hash) {
      const index = sections.findIndex(s => s.id === hash);
      if (index !== -1) setCurrentSection(index);
    }
    // Aggiorna la sezione quando cambia l'hash manualmente
    const onHashChange = () => {
      const hash = window.location.hash.replace('#', '');
      const index = sections.findIndex(s => s.id === hash);
      if (index !== -1) setCurrentSection(index);
    };
    window.addEventListener('hashchange', onHashChange);
    return () => window.removeEventListener('hashchange', onHashChange);
  }, [isMobile]);

  // Aggiorna l'hash quando cambia la sezione
  useEffect(() => {
    if (isMobile) return;
    if (sections[currentSection]) {
      window.location.hash = sections[currentSection].id;
    }
  }, [currentSection, sections, isMobile]);

  // Scroll alla sezione su mobile
  const scrollToSection = (index) => {
    if (isMobile) {
      const el = sectionRefs.current[index];
      if (el) {
        el.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
      setCurrentSection(index); // aggiorna comunque per la navbar
      setFooterVisible(false);
    } else {
      setFooterVisible(false);
      setCurrentSection(index);
    }
  };

  if (isLoading) {
    return <ProfessionalLoader onComplete={() => setIsLoading(false)} />;
  }

  return (
    <div className="relative bg-slate-900 text-white font-sans antialiased">
      <ProfessionalParticleSystem />
      <ProfessionalHeader 
        currentSection={currentSection}
        navigateToSection={scrollToSection}
        sections={sections}
      />
      
      {/* Main content */}
      {!isMobile ? (
        <div 
          ref={mainContainerRef}
          className="flex w-full h-screen transition-transform duration-700 ease-in-out md:flex-row flex-col"
          style={{ 
            transform: `translateX(-${currentSection * 100}vw)`,
            height: '100vh',
          }}
        >
          {sections.map((section, index) => (
            <div 
              key={section.id} 
              ref={el => sectionRefs.current[index] = el}
              className="w-screen flex-shrink-0 h-screen overflow-y-auto min-h-[100dvh] max-h-none"
              style={{
                minHeight: '100dvh',
                maxHeight: 'none',
                height: '100%',
              }}
            >
              {section.component}
            </div>
          ))}
        </div>
      ) : (
        // MOBILE: tutte le sezioni in verticale
        <div className="flex flex-col w-full min-h-screen">
          {sections.map((section, index) => (
            <div
              key={section.id}
              ref={el => sectionRefs.current[index] = el}
              className="w-full min-h-[100dvh] max-h-none overflow-y-auto"
              style={{ minHeight: '100dvh', maxHeight: 'none' }}
            >
              {section.component}
            </div>
          ))}
          {/* Footer sempre visibile in fondo su mobile */}
          <div className="w-full bg-slate-900">
            <ProfessionalFooter 
              setFooterVisible={setFooterVisible}
              setCurrentSection={setCurrentSection}
            />
          </div>
        </div>
      )}

      {/* Footer section solo desktop */}
      {!isMobile && (
        <div 
          ref={footerRef}
          className={`w-full min-h-screen bg-slate-900 transition-all duration-700 ${
            footerVisible ? 'block' : 'hidden'
          }`}
          style={{
            position: 'relative',
            minHeight: '100vh',
          }}
        >
          <ProfessionalFooter 
            setFooterVisible={setFooterVisible}
            setCurrentSection={setCurrentSection}
          />
        </div>
      )}

      {/* Navigation Dots e Arrows solo desktop */}
      {!isMobile && (
        <>
          <div className="fixed bottom-8 left-1/2 transform -translate-x-1/2 flex space-x-3 z-50">
            {sections.map((_, index) => (
              <button
                key={index}
                onClick={() => { setFooterVisible(false); setCurrentSection(index); }}
                className={`w-3 h-3 rounded-full transition-all duration-300 ${
                  currentSection === index && !footerVisible
                    ? 'bg-blue-500 scale-125'
                    : 'bg-slate-600 hover:bg-slate-400'
                }`}
              />
            ))}
          </div>
          <div className="fixed top-1/2 left-4 transform -translate-y-1/2 z-50">
            <button
              onClick={() => !footerVisible && currentSection > 0 && setCurrentSection(currentSection - 1)}
              className={`p-3 rounded-full backdrop-blur-lg transition-all duration-300 ${
                !footerVisible && currentSection > 0
                  ? 'bg-slate-800/50 text-white hover:bg-slate-700/70'
                  : 'bg-slate-800/20 text-slate-600 cursor-not-allowed'
              }`}
              disabled={footerVisible || currentSection === 0}
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
          </div>
          <div className="fixed top-1/2 right-4 transform -translate-y-1/2 z-50">
            <button
              onClick={() => !footerVisible && currentSection < sections.length - 1 && setCurrentSection(currentSection + 1)}
              className={`p-3 rounded-full backdrop-blur-lg transition-all duration-300 ${
                !footerVisible && currentSection < sections.length - 1
                  ? 'bg-slate-800/50 text-white hover:bg-slate-700/70'
                  : 'bg-slate-800/20 text-slate-600 cursor-not-allowed'
              }`}
              disabled={footerVisible || currentSection === sections.length - 1}
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
              </svg>
            </button>
          </div>
        </>
      )}
    </div>
  );
};

export default App;

// Global styles
const styles = `
  /* Scrollbar personalizzata */
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
  }
  
  ::-webkit-scrollbar-track {
    background: rgba(30, 41, 59, 0.3);
  }
  
  ::-webkit-scrollbar-thumb {
    background: rgba(59, 130, 246, 0.5);
    border-radius: 4px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgba(59, 130, 246, 0.8);
  }
  
  /* Animazione per il loop */
  @keyframes loopPrompt {
    0%, 100% { transform: translateY(0); opacity: 0.7; }
    50% { transform: translateY(-10px); opacity: 1; }
  }
`;

// Inject styles
const styleSheet = document.createElement("style");
styleSheet.type = "text/css";
styleSheet.innerText = styles;
document.head.appendChild(styleSheet);