import React, { useState } from 'react';
import { Eye, Globe, Cpu, Database, Shield, Award, Rocket, Users, Layers, TrendingUp, BarChart3 } from 'lucide-react';
import useScrollAnimation from '../hooks/useScrollAnimation';


const ProfessionalAbout = () => {
  const [ref, isVisible] = useScrollAnimation(0.2);
  const [activeYear, setActiveYear] = useState(2023);

  const milestones = [
    {
      year: 2024,
      title: "Prototype Development",
      description: "First working prototype using lightweight hardware: Raspberry Pi 5, ESP32 with LoRa module, ESP32 PoE, and Raspberry Pi Camera. Focus on distributed road hazard detection and alerting.",
      icon: <Cpu className="w-6 h-6" />,
      color: "bg-cyan-500"
    },
    {
      year: 2023,
      title: "Concept & Architecture",
      description: "System design for distributed road safety alerts. Emphasis on affordable, low-power devices and open communication between vehicles.",
      icon: <Globe className="w-6 h-6" />,
      color: "bg-blue-500"
    }
    // ...other milestones if needed...
  ];

  return (
    <section ref={ref} id="about" className="py-24 relative overflow-hidden">
      <div className="absolute inset-0 bg-gradient-to-b from-slate-900/50 to-transparent" />
      
      <div className="container mx-auto px-6 relative z-10">
        <div className={`text-center mb-20 transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <div className="inline-flex items-center gap-2 bg-indigo-500/10 border border-indigo-500/30 px-4 py-2 rounded-full text-indigo-400 text-sm font-medium mb-6">
            <Eye size={16} />
            Project Overview
          </div>
          <h2 className="text-4xl md:text-6xl font-bold text-white mb-8 leading-tight">
            Distributed
            <span className="block bg-gradient-to-r from-indigo-400 to-blue-400 bg-clip-text text-transparent">
              Road Safety Prototype
            </span>
          </h2>
          <p className="text-xl text-slate-300 max-w-3xl mx-auto leading-relaxed">
            We are building a distributed road hazard alert system using lightweight, affordable hardware such as Raspberry Pi 5, ESP32 LoRa, ESP32 PoE, and Raspberry Pi Camera. The system is not instantaneous, but is designed for real-world feasibility and low power consumption. Currently, this is a prototype and we do not have clients yet.
          </p>
          {/* Placeholder for hardware images or system diagrams */}
          <div className="flex justify-center gap-6 mt-10 mb-4">
            {/* Example: */}
            {/* <img src="/images/rpi5.jpg" alt="Raspberry Pi 5" className="w-32 h-24 object-contain rounded shadow" /> */}
            {/* <img src="/images/esp32-lora.jpg" alt="ESP32 LoRa" className="w-32 h-24 object-contain rounded shadow" /> */}
            {/* <img src="/images/system-diagram.png" alt="System Diagram" className="w-40 h-24 object-contain rounded shadow" /> */}
            {/* Add your images here */}
          </div>
        </div>

        <div className="flex flex-col lg:flex-row gap-12">
          <div className="lg:w-1/3">
            <div className="sticky top-32">
              <div className="relative">
                <div className="absolute -left-1 top-0 bottom-0 w-1 bg-slate-700/50 rounded-full" />
                
                {milestones.map((milestone, index) => (
                  <button
                    key={milestone.year}
                    onClick={() => setActiveYear(milestone.year)}
                    className={`relative flex items-center gap-4 w-full p-4 rounded-lg transition-all duration-300 ${
                      activeYear === milestone.year 
                        ? 'bg-slate-800/50 backdrop-blur-sm border border-slate-700/50 shadow-lg' 
                        : 'hover:bg-slate-800/30'
                    }`}
                    style={{ marginBottom: index === milestones.length - 1 ? 0 : '1rem' }}
                  >
                    <div className={`w-10 h-10 rounded-full flex items-center justify-center ${milestone.color} text-white shadow-md`}>
                      {milestone.icon}
                    </div>
                    <div className="text-left">
                      <div className={`font-bold transition-colors duration-300 ${
                        activeYear === milestone.year ? 'text-white' : 'text-slate-300'
                      }`}>
                        {milestone.year}
                      </div>
                      <div className={`text-sm transition-colors duration-300 ${
                        activeYear === milestone.year ? 'text-blue-300' : 'text-slate-400'
                      }`}>
                        {milestone.title}
                      </div>
                    </div>
                    <div className={`absolute -left-1 top-1/2 transform -translate-y-1/2 w-3 h-3 rounded-full transition-all duration-300 ${
                      activeYear === milestone.year ? 'bg-blue-400 scale-150' : 'bg-slate-500'
                    }`} />
                  </button>
                ))}
              </div>
            </div>
          </div>

          <div className="lg:w-2/3">
            {milestones.filter(m => m.year === activeYear).map(milestone => (
              <div 
                key={milestone.year} 
                className={`bg-gradient-to-br from-slate-800/50 to-slate-900/50 backdrop-blur-sm border border-slate-700/50 rounded-xl p-8 transition-all duration-700 ${
                  isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'
                }`}
              >
                <div className="flex items-center gap-4 mb-6">
                  <div className={`w-16 h-16 rounded-xl flex items-center justify-center ${milestone.color} text-white shadow-lg`}>
                    {milestone.icon}
                  </div>
                  <div>
                    <h3 className="text-2xl font-bold text-white">{milestone.title}</h3>
                    <div className="text-blue-400 font-medium">{milestone.year}</div>
                  </div>
                </div>
                
                <p className="text-slate-300 leading-relaxed mb-8">
                  {milestone.description}
                </p>

                {/* Hardware/Diagram Images by Year */}
                <div className="flex flex-wrap gap-6 mb-8">
                  {milestone.year === 2024 && (
                    <>
                      {/* Replace src with your actual image paths */}
                      <img src="/images/rpi5.jpg" alt="Raspberry Pi 5" className="w-32 h-24 object-contain rounded shadow" />
                      <img src="/images/esp32-lora.jpg" alt="ESP32 LoRa" className="w-32 h-24 object-contain rounded shadow" />
                      <img src="/images/esp32-poe.jpg" alt="ESP32 PoE" className="w-32 h-24 object-contain rounded shadow" />
                      <img src="/images/pi-camera.jpg" alt="Raspberry Pi Camera" className="w-32 h-24 object-contain rounded shadow" />
                      <img src="/images/system-diagram-2024.png" alt="System Diagram 2024" className="w-40 h-24 object-contain rounded shadow" />
                    </>
                  )}
                  {milestone.year === 2023 && (
                    <>
                      <img src="/images/architecture-2023.png" alt="System Architecture 2023" className="w-40 h-24 object-contain rounded shadow" />
                    </>
                  )}
                </div>
                
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {[
                    { label: "Clienti Acquisiti", value: "120+", icon: <Users size={16} /> },
                    { label: "Nuovi Prodotti", value: "8", icon: <Layers size={16} /> },
                    { label: "Team Growth", value: "45%", icon: <TrendingUp size={16} /> },
                    { label: "Revenue Increase", value: "2.5x", icon: <BarChart3 size={16} /> }
                  ].map((stat, index) => (
                    <div key={index} className="bg-slate-800/30 border border-slate-700/50 rounded-lg p-4 flex items-center gap-4">
                      <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${milestone.color}/20 text-${milestone.color.split('-')[1]}-400`}>
                        {stat.icon}
                      </div>
                      <div>
                        <div className="text-sm text-slate-400">{stat.label}</div>
                        <div className="text-xl font-bold text-white">{stat.value}</div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProfessionalAbout;
