import React, { useRef, useEffect } from 'react';

const ProfessionalParticleSystem = () => {
  const canvasRef = useRef(null);
    const animationRef = useRef(null);
    const particlesRef = useRef([]);
  
    useEffect(() => {
      const canvas = canvasRef.current;
      const ctx = canvas.getContext('2d');
      
      const resizeCanvas = () => {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
      };
      
      resizeCanvas();
      window.addEventListener('resize', resizeCanvas);
  
      // Initialize professional particles
      for (let i = 0; i < 40; i++) {
        particlesRef.current.push({
          x: Math.random() * canvas.width,
          y: Math.random() * canvas.height,
          vx: (Math.random() - 0.5) * 0.5,
          vy: (Math.random() - 0.5) * 0.5,
          size: Math.random() * 2 + 0.5,
          alpha: Math.random() * 0.3 + 0.1,
          color: `hsl(${220 + Math.random() * 40}, 60%, 70%)`,
        });
      }
  
      const animate = () => {
        ctx.fillStyle = 'rgba(0, 5, 15, 0.05)';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
  
        particlesRef.current.forEach(particle => {
          particle.x += particle.vx;
          particle.y += particle.vy;
  
          if (particle.x < 0 || particle.x > canvas.width) particle.vx *= -1;
          if (particle.y < 0 || particle.y > canvas.height) particle.vy *= -1;
  
          const gradient = ctx.createRadialGradient(particle.x, particle.y, 0, particle.x, particle.y, particle.size * 2);
          gradient.addColorStop(0, `${particle.color.slice(0, -1)}, ${particle.alpha})`);
          gradient.addColorStop(1, `${particle.color.slice(0, -1)}, 0)`);
          
          ctx.fillStyle = gradient;
          ctx.beginPath();
          ctx.arc(particle.x, particle.y, particle.size * 2, 0, Math.PI * 2);
          ctx.fill();
        });
  
        animationRef.current = requestAnimationFrame(animate);
      };
  
      animate();
  
      return () => {
        window.removeEventListener('resize', resizeCanvas);
        if (animationRef.current) {
          cancelAnimationFrame(animationRef.current);
        }
      };
    }, []);
  
    return (
      <canvas
        ref={canvasRef}
        className="fixed inset-0 pointer-events-none z-0"
        style={{ background: 'linear-gradient(135deg, #0a0f1c 0%, #1a1f2e 50%, #0f1419 100%)' }}
      />
    );
};

export default ProfessionalParticleSystem;
