import React, { useState, useEffect } from 'react';

const ProfessionalLoader = ({ onComplete }) => {
  const [progress, setProgress] = useState(0);
    const [stage, setStage] = useState(0);
    const stages = ['INITIALIZING SYSTEM', 'LOADING COMPONENTS', 'PREPARING INTERFACE', 'LAUNCHING APPLICATION'];
  
    useEffect(() => {
      const interval = setInterval(() => {
        setProgress(prev => {
          const newProgress = prev + 2;
          const newStage = Math.floor((newProgress / 100) * stages.length);
          if (newStage !== stage && newStage < stages.length) {
            setStage(newStage);
          }
          
          if (newProgress >= 100) {
            clearInterval(interval);
            setTimeout(onComplete, 500);
            return 100;
          }
          return newProgress;
        });
      }, 40);
  
      return () => clearInterval(interval);
    }, [stage, onComplete]);
  
    return (
      <div className="fixed inset-0 bg-gradient-to-br from-slate-900 via-blue-900 to-slate-800 flex items-center justify-center z-50">
        <div className="text-center">
          <div className="relative mb-12">
            <h1 className="text-7xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-blue-400 to-cyan-300 tracking-wide">
              VIPRA
            </h1>
            <div className="absolute -inset-2 bg-gradient-to-r from-blue-500/20 to-cyan-500/20 blur-xl rounded-lg opacity-50"></div>
          </div>
  
          <div className="w-96 mx-auto mb-8">
            <div className="h-1 bg-slate-700 rounded-full overflow-hidden">
              <div 
                className="h-full bg-gradient-to-r from-blue-500 to-cyan-400 transition-all duration-300 ease-out rounded-full"
                style={{ width: `${progress}%` }}
              />
            </div>
            <div className="flex justify-between mt-4 text-sm text-slate-400">
              <span>{stages[stage]}</span>
              <span className="font-mono">{Math.floor(progress)}%</span>
            </div>
          </div>
  
          <div className="flex justify-center space-x-1">
            {[...Array(4)].map((_, i) => (
              <div
                key={i}
                className="w-2 h-2 bg-blue-400 rounded-full animate-pulse"
                style={{ animationDelay: `${i * 0.15}s` }}
              />
            ))}
          </div>
        </div>
      </div>
    );
};

export default ProfessionalLoader;
