import React from 'react';
import { FileText, ArrowRight, Download } from 'lucide-react';

const ProfessionalRelazione = () => {
  return (
    <section className="h-screen flex items-center justify-center bg-gradient-to-br from-slate-900 via-blue-900/20 to-slate-900 relative overflow-hidden">
      {/* Decorazioni di sfondo */}
      <div className="absolute inset-0 overflow-hidden">
        {[...Array(8)].map((_, i) => (
          <div
            key={i}
            className="absolute rounded-full bg-blue-500/10 animate-float"
            style={{
              width: `${Math.random() * 200 + 100}px`,
              height: `${Math.random() * 200 + 100}px`,
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animationDelay: `${i * 0.5}s`,
              animationDuration: `${10 + i * 2}s`,
            }}
          />
        ))}
      </div>

      <div className="container mx-auto px-6 text-center relative z-10 max-w-4xl">
        <div className="mb-12 inline-block p-4 bg-gradient-to-r from-blue-500/20 to-cyan-500/20 rounded-2xl border border-blue-500/30">
          <FileText size={48} className="text-blue-400 mx-auto" />
        </div>

        <h2 className="text-4xl md:text-6xl font-bold text-white mb-6">
          <span className="bg-gradient-to-r from-blue-400 to-cyan-400 bg-clip-text text-transparent">
            Documentation
          </span>
        </h2>

        <p className="text-xl text-slate-300 mb-12 max-w-2xl mx-auto leading-relaxed">
          Explore the technical documentation for our distributed road safety prototype. Learn about the use of Raspberry Pi 5, ESP32 LoRa, ESP32 PoE, and Pi Camera, and view system diagrams and hardware images.
        </p>

        <div className="flex flex-wrap justify-center gap-6">
          <a
            href="/relazione.pdf" // Sostituisci con il percorso effettivo
            download
            className="group flex items-center gap-3 bg-gradient-to-r from-blue-600 to-cyan-600 hover:from-blue-700 hover:to-cyan-700 text-white px-8 py-4 rounded-lg font-semibold text-lg transition-all duration-300 transform hover:scale-105 shadow-xl"
          >
            <Download size={20} />
            Download Report
            <ArrowRight className="group-hover:translate-x-1 transition-transform duration-300" size={20} />
          </a>

          <a
            href="/relazione" // Sostituisci con il percorso effettivo
            className="group flex items-center gap-3 border-2 border-blue-500 text-blue-400 hover:bg-blue-500 hover:text-white px-8 py-4 rounded-lg font-semibold text-lg transition-all duration-300 transform hover:scale-105"
          >
            <FileText size={20} />
            View Online
          </a>
        </div>
      </div>
    </section>
  );
};

export default ProfessionalRelazione;