import React, { useState, useEffect } from 'react';
import { Star, Code, Shield, Zap, Database, Layers, Target, ArrowRight, Sparkles, CheckCircle, Bolt } from 'lucide-react';

const EnhancedProfessionalFeatures = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [hoveredCard, setHoveredCard] = useState(null);
  const [activeFeature, setActiveFeature] = useState(0);

  const features = [
    {
      icon: <Code className="w-8 h-8" />,
      title: 'Lightweight Distributed Detection',
      description: 'Each vehicle prototype uses Raspberry Pi 5, ESP32 with LoRa, ESP32 PoE, and a Raspberry Pi Camera to detect road hazards and broadcast alerts to nearby vehicles.',
      gradient: 'from-blue-500 via-cyan-500 to-teal-400',
      stats: 'Prototype Hardware',
      delay: 0,
      tags: ['Raspberry Pi 5', 'ESP32 LoRa', 'ESP32 PoE', 'Pi Camera'],
      achievement: 'Low Power'
    },
    {
      icon: <Shield className="w-8 h-8" />,
      title: 'Non-Instantaneous Alerts',
      description: 'Due to the use of affordable, low-power hardware, alerts are not instantaneous but are optimized for practical deployment and energy efficiency.',
      gradient: 'from-emerald-500 via-teal-500 to-cyan-500',
      stats: 'Energy Efficient',
      delay: 0.1,
      tags: ['LoRa', 'Low Power', 'Affordable'],
      achievement: 'Feasible Design'
    },
    {
      icon: <Zap className="w-8 h-8" />,
      title: 'Open Broadcast Communication',
      description: 'Detected hazards are broadcasted to all vehicles in range using LoRa technology, enabling open and distributed warning even with limited connectivity.',
      gradient: 'from-yellow-500 via-orange-500 to-red-500',
      stats: 'LoRa Broadcast',
      delay: 0.2,
      tags: ['LoRa', 'Broadcast', 'Open Protocol'],
      achievement: 'Wide Coverage'
    },
    {
      icon: <Database className="w-8 h-8" />,
      title: 'Data Logging & Analysis',
      description: 'All events and detections are logged for later analysis, supporting system improvement and validation of the prototype.',
      gradient: 'from-purple-500 via-violet-500 to-indigo-500',
      stats: 'Event Logging',
      delay: 0.3,
      tags: ['Data', 'Logging', 'Analysis'],
      achievement: 'Prototype Validation'
    },
    {
      icon: <Layers className="w-8 h-8" />,
      title: 'Modular & Expandable',
      description: 'The system is designed to be modular, allowing easy integration of new sensors or hardware upgrades as technology evolves.',
      gradient: 'from-blue-600 via-indigo-500 to-purple-500',
      stats: 'Expandable',
      delay: 0.4,
      tags: ['Modular', 'Upgradable', 'Flexible'],
      achievement: 'Future Ready'
    },
    {
      icon: <Target className="w-8 h-8" />,
      title: 'Research-Driven Approach',
      description: 'This project is currently a prototype and part of ongoing research. No commercial deployments or clients yet.',
      gradient: 'from-pink-500 via-rose-500 to-red-500',
      stats: 'Prototype Stage',
      delay: 0.5,
      tags: ['Research', 'Prototype', 'No Clients'],
      achievement: 'In Progress'
    }
  ];

  useEffect(() => {
    const timer = setTimeout(() => setIsVisible(true), 200);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      setActiveFeature((prev) => (prev + 1) % features.length);
    }, 4000);
    return () => clearInterval(interval);
  }, []);

  return (
    <section className="min-h-screen flex items-center justify-center py-16 px-8 relative overflow-hidden">
      {/* Dynamic Background */}
      <div className="absolute inset-0 bg-gradient-to-br from-slate-900 via-blue-900/20 to-slate-900" />
      
      {/* Animated Background Elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(8)].map((_, i) => (
          <div
            key={i}
            className="absolute opacity-5 animate-float"
            style={{
              left: `${10 + (i * 12)}%`,
              top: `${5 + (i * 15)}%`,
              animationDelay: `${i * 0.5}s`,
              animationDuration: `${6 + i}s`
            }}
          >
            <div className={`w-32 h-32 rounded-full bg-gradient-to-br ${features[i % features.length].gradient}`} />
          </div>
        ))}
      </div>

      <div className="container mx-auto relative z-10 max-w-7xl">
        {/* Header */}
        <br/>
        <div className={`text-center mb-20 transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <div className="inline-flex items-center gap-3 bg-gradient-to-r from-blue-500/10 to-purple-500/10 border border-blue-500/30 px-6 py-3 rounded-full text-blue-400 text-sm font-medium mb-8 backdrop-blur-sm">
            <Star size={20} className="animate-spin" style={{ animationDuration: '3s' }} />
            <Bolt size={16} className="animate-pulse" />
            Key Features
          </div>
          
          <h2 className="text-5xl md:text-7xl font-black text-white mb-8 leading-tight">
            Advanced Distributed
            <span className="block bg-gradient-to-r from-blue-400 via-cyan-400 to-purple-400 bg-clip-text text-transparent">
              Road Safety System
            </span>
          </h2>
          
          <p className="text-xl text-slate-300 max-w-4xl mx-auto leading-relaxed mb-8">
            Our distributed system empowers vehicles to detect, share, and respond to road hazards in real time, making every journey safer and more informed.
          </p>

          {/* Feature Indicators */}
          <div className="flex justify-center gap-2 mb-8">
            {features.map((_, index) => (
              <div
                key={index}
                className={`h-1 transition-all duration-500 rounded-full ${
                  activeFeature === index ? 'w-8 bg-blue-400' : 'w-4 bg-slate-600'
                }`}
              />
            ))}
          </div>
        </div>

        {/* Features Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-3 gap-8">
          {features.map((feature, index) => (
            <div
              key={index}
              className={`group relative transition-all duration-700 ${
                isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'
              }`}
              style={{ transitionDelay: `${feature.delay}s` }}
              onMouseEnter={() => setHoveredCard(index)}
              onMouseLeave={() => setHoveredCard(null)}
            >
              <div className={`relative bg-gradient-to-br from-slate-800/60 to-slate-900/80 backdrop-blur-lg p-8 rounded-2xl border transition-all duration-500 transform ${
                hoveredCard === index || activeFeature === index
                  ? 'border-blue-500/60 scale-105 shadow-2xl shadow-blue-500/20' 
                  : 'border-slate-700/50 hover:border-blue-500/40'
              }`}>
                
                {/* Glow Effect */}
                <div className={`absolute inset-0 bg-gradient-to-br ${feature.gradient} opacity-0 ${
                  hoveredCard === index || activeFeature === index ? 'opacity-10' : 'group-hover:opacity-5'
                } rounded-2xl transition-opacity duration-500`} />
                
                {/* Active Feature Pulse */}
                {activeFeature === index && (
                  <div className="absolute inset-0 rounded-2xl border-2 border-blue-400/40 animate-pulse" />
                )}
                
                {/* Icon with Enhanced Animation */}
                <div className={`relative mb-6 inline-flex items-center justify-center w-18 h-18 rounded-xl bg-gradient-to-br ${feature.gradient} shadow-xl transform transition-all duration-300 ${
                  hoveredCard === index ? 'scale-110 rotate-3' : 'group-hover:scale-105'
                }`}>
                  <span className="text-white relative z-10">
                    {feature.icon}
                  </span>
                  {hoveredCard === index && (
                    <Sparkles className="absolute -top-1 -right-1 w-4 h-4 text-yellow-400 animate-bounce" />
                  )}
                </div>

                {/* Content */}
                <div className="relative z-10">
                  <div className="flex items-center justify-between mb-4">
                    <h3 className={`text-xl font-bold transition-colors duration-300 ${
                      hoveredCard === index || activeFeature === index ? 'text-blue-300' : 'text-white'
                    }`}>
                      {feature.title}
                    </h3>
                    <span className={`text-xs font-mono px-3 py-1 rounded-full bg-gradient-to-r ${feature.gradient} text-white/90 shadow-lg`}>
                      {feature.stats}
                    </span>
                  </div>
                  
                  <p className={`text-slate-300 leading-relaxed mb-6 transition-colors duration-300 ${
                    hoveredCard === index ? 'text-slate-200' : ''
                  }`}>
                    {feature.description}
                  </p>

                  {/* Technology Tags */}
                  <div className="flex flex-wrap gap-2 mb-6">
                    {feature.tags.map((tag, tagIndex) => (
                      <span
                        key={tagIndex}
                        className="text-xs px-2 py-1 bg-slate-700/50 text-slate-300 rounded-md border border-slate-600/50"
                      >
                        {tag}
                      </span>
                    ))}
                  </div>

                  {/* Achievement Badge */}
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2 text-emerald-400 text-sm font-medium">
                      <CheckCircle size={16} />
                      {feature.achievement}
                    </div>
                    
                    <button className={`flex items-center gap-2 font-medium transition-all duration-300 transform ${
                      hoveredCard === index 
                        ? 'text-white translate-x-2' 
                        : 'text-blue-400 hover:text-white group-hover:translate-x-1'
                    }`}>
                      Scopri di pi
                      <ArrowRight size={16} className="transition-transform duration-300" />
                    </button>
                  </div>
                </div>

                {/* Decorative Corner */}
                <div className="absolute top-4 right-4 opacity-20 group-hover:opacity-40 transition-opacity duration-300">
                  <div className={`w-8 h-8 border-2 border-current rounded-full animate-spin text-blue-400`} 
                       style={{ animationDuration: '4s' }} />
                </div>

                {/* Progress Indicator */}
                <div className="absolute bottom-0 left-0 right-0 h-1 bg-slate-700/30 rounded-b-2xl overflow-hidden">
                  <div 
                    className={`h-full bg-gradient-to-r ${feature.gradient} transition-all duration-1000 ease-out`}
                    style={{ 
                      width: `${(hoveredCard === index || activeFeature === index) ? '100%' : '0%'}`,
                    }}
                  />
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Bottom CTA */}
        <div className={`mt-16 text-center transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`} style={{ transitionDelay: '600ms' }}>
          <button className="inline-flex items-center gap-3 bg-gradient-to-r from-blue-600 via-purple-600 to-cyan-600 hover:from-blue-700 hover:via-purple-700 hover:to-cyan-700 text-white px-8 py-4 rounded-xl font-semibold transition-all duration-300 transform hover:scale-105 shadow-xl">
            <Zap className="w-5 h-5" />
            Request a Demo
            <ArrowRight className="w-5 h-5" />
          </button>
        </div>
      </div>

      <style jsx>{`
        @keyframes float {
          0%, 100% { transform: translateY(0px) rotate(0deg); }
          50% { transform: translateY(-20px) rotate(5deg); }
        }
        .animate-float {
          animation: float 6s ease-in-out infinite;
        }
      `}</style>
    </section>
  );
};

export default EnhancedProfessionalFeatures;