import React, { useState, useEffect } from 'react';
import { Users, CheckCircle, ArrowRight, Mail, Phone, MapPin, Send, Sparkles, Globe, Award, Heart } from 'lucide-react';

// Enhanced ProfessionalContact Component
const ProfessionalContact = ({ isLastSection = false }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
    company: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    const handleMouseMove = (e) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };

    const handleScroll = () => {
      if (isLastSection) {
        setScrollY(window.scrollY);
      }
    };

    window.addEventListener('mousemove', handleMouseMove);
    window.addEventListener('scroll', handleScroll);
    
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('scroll', handleScroll);
    };
  }, [isLastSection]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSubmitting(true);
    
    setTimeout(() => {
      setIsSubmitting(false);
      setSubmitSuccess(true);
      setFormData({ name: '', email: '', message: '', company: '' });
      
      setTimeout(() => setSubmitSuccess(false), 5000);
    }, 2000);
  };

  return (
    <section className={`min-h-screen relative overflow-hidden ${isLastSection ? 'pb-0' : 'pb-24'}`}>
      {/* Animated Background */}
      <div className="absolute inset-0">
        <div className="absolute inset-0 bg-gradient-to-br from-slate-900 via-blue-900/20 to-cyan-900/30" />
        
        {/* Floating Orbs */}
        {[...Array(6)].map((_, i) => (
          <div
            key={i}
            className="absolute rounded-full blur-xl animate-pulse"
            style={{
              width: `${Math.random() * 200 + 100}px`,
              height: `${Math.random() * 200 + 100}px`,
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              background: `radial-gradient(circle, ${
                i % 2 === 0 ? 'rgba(59, 130, 246, 0.1)' : 'rgba(34, 211, 238, 0.1)'
              } 0%, transparent 70%)`,
              animationDelay: `${i * 0.5}s`,
              animationDuration: `${3 + i}s`,
            }}
          />
        ))}

        {/* Interactive Cursor Effect */}
        <div
          className="absolute w-64 h-64 rounded-full pointer-events-none transition-all duration-1000 ease-out"
          style={{
            left: mousePosition.x - 128,
            top: mousePosition.y - 128,
            background: 'radial-gradient(circle, rgba(59, 130, 246, 0.05) 0%, transparent 70%)',
            filter: 'blur(40px)',
          }}
        />
      </div>

      <div className="relative z-10 container mx-auto px-6 py-24 min-h-screen flex flex-col justify-center">
        {/* Header Section */}
        <div className="text-center mb-20 animate-fade-in-up">
          <div className="inline-flex items-center gap-3 bg-gradient-to-r from-blue-500/10 to-cyan-500/10 border border-blue-500/30 backdrop-blur-sm px-6 py-3 rounded-full text-blue-400 text-sm font-medium mb-8 hover:scale-105 transition-transform duration-300">
            <Sparkles size={18} className="animate-spin-slow" />
            Let's Connect
            <Users size={18} />
          </div>
          
          <h2 className="text-5xl md:text-7xl font-bold text-white mb-8 leading-tight">
            <span className="inline-block animate-slide-in-left">Transform</span>{' '}
            <span className="inline-block animate-slide-in-right delay-200">Your</span>{' '}
            <span className="inline-block animate-slide-in-left delay-400">Road</span>
            <span className="block bg-gradient-to-r from-blue-400 via-cyan-400 to-purple-400 bg-clip-text text-transparent animate-gradient-x">
              Safety Experience
            </span>
          </h2>
          
          <p className="text-xl text-slate-300 max-w-4xl mx-auto leading-relaxed animate-fade-in-up delay-600">
            Our team is ready to help you bring advanced distributed safety to your vehicles.
            <br />
            <span className="text-blue-400 font-medium">Start your journey towards safer roads today.</span>
          </p>
        </div>

        {/* Main Content */}
        <div className="max-w-7xl mx-auto w-full">
          <div className="bg-gradient-to-br from-slate-800/30 to-slate-900/50 backdrop-blur-xl border border-slate-700/30 rounded-3xl overflow-hidden shadow-2xl hover:shadow-blue-500/10 transition-all duration-700 animate-scale-in">
            <div className="grid grid-cols-1 lg:grid-cols-5 gap-0 min-h-[600px]">
              
              {/* Contact Form */}
              <div className="lg:col-span-3 p-8 lg:p-12">
                <div className="flex items-center gap-3 mb-8">
                  <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-blue-500 to-cyan-500 flex items-center justify-center shadow-lg">
                    <Send className="w-6 h-6 text-white" />
                  </div>
                  <h3 className="text-3xl font-bold text-white">Share Your Vision</h3>
                </div>
                
                {submitSuccess ? (
                  <div className="bg-gradient-to-r from-green-500/10 to-emerald-500/10 border border-green-500/30 text-green-400 p-8 rounded-2xl mb-6 animate-bounce-in">
                    <div className="flex items-center gap-4 mb-4">
                      <div className="w-12 h-12 rounded-full bg-green-500/20 flex items-center justify-center">
                        <CheckCircle className="w-6 h-6" />
                      </div>
                      <div>
                        <h4 className="text-xl font-bold">Messaggio Inviato con Successo!</h4>
                        <p className="text-green-300">Il nostro team ti contatter entro 24 ore.</p>
                      </div>
                    </div>
                  </div>
                ) : (
                  <form onSubmit={handleSubmit} className="space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <div className="group">
                        <label className="block text-sm font-semibold text-slate-300 mb-3">Full Name *</label>
                        <input
                          type="text"
                          name="name"
                          value={formData.name}
                          onChange={handleChange}
                          required
                          className="w-full bg-slate-800/50 border border-slate-700/50 rounded-xl px-6 py-4 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 hover:bg-slate-800/70 group-hover:border-slate-600/50"
                          placeholder="Mario Rossi"
                        />
                      </div>
                      
                      <div className="group">
                        <label className="block text-sm font-semibold text-slate-300 mb-3">Company</label>
                        <input
                          type="text"
                          name="company"
                          value={formData.company}
                          onChange={handleChange}
                          className="w-full bg-slate-800/50 border border-slate-700/50 rounded-xl px-6 py-4 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 hover:bg-slate-800/70 group-hover:border-slate-600/50"
                          placeholder="La Tua Azienda SRL"
                        />
                      </div>
                    </div>
                    
                    <div className="group">
                      <label className="block text-sm font-semibold text-slate-300 mb-3">Email *</label>
                      <input
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                        className="w-full bg-slate-800/50 border border-slate-700/50 rounded-xl px-6 py-4 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 hover:bg-slate-800/70 group-hover:border-slate-600/50"
                        placeholder="mario@azienda.com"
                      />
                    </div>
                    
                    <div className="group">
                      <label className="block text-sm font-semibold text-slate-300 mb-3">Your Message *</label>
                      <textarea
                        name="message"
                        value={formData.message}
                        onChange={handleChange}
                        required
                        rows="5"
                        className="w-full bg-slate-800/50 border border-slate-700/50 rounded-xl px-6 py-4 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300 hover:bg-slate-800/70 group-hover:border-slate-600/50 resize-none"
                        placeholder="Tell us about your needs, your goals, and how we can help you make roads safer..."
                      />
                    </div>
                    
                    <button
                      type="submit"
                      disabled={isSubmitting}
                      className="w-full group relative overflow-hidden bg-gradient-to-r from-blue-600 via-cyan-600 to-purple-600 hover:from-blue-700 hover:via-cyan-700 hover:to-purple-700 text-white px-8 py-5 rounded-xl font-bold text-lg transition-all duration-500 transform hover:scale-[1.02] hover:shadow-2xl hover:shadow-blue-500/25 disabled:opacity-80 disabled:cursor-not-allowed"
                    >
                      <span className="absolute inset-0 bg-gradient-to-r from-white/10 to-transparent transform -skew-x-12 -translate-x-full group-hover:translate-x-full transition-transform duration-1000" />
                      
                      <div className="relative flex items-center justify-center gap-3">
                        {isSubmitting ? (
                          <>
                            <div className="w-6 h-6 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                            Invio in corso...
                          </>
                        ) : (
                          <>
                            <Send size={20} className="group-hover:rotate-12 transition-transform duration-300" />
                            Send Your Message
                            <ArrowRight size={20} className="group-hover:translate-x-1 transition-transform duration-300" />
                          </>
                        )}
                      </div>
                    </button>
                  </form>
                )}
              </div>
              
              {/* Contact Info */}
              <div className="lg:col-span-2 bg-gradient-to-br from-blue-900/40 to-purple-900/40 backdrop-blur-sm p-8 lg:p-12 flex flex-col justify-between relative overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-br from-blue-500/5 to-purple-500/5" />
                <div className="absolute top-0 right-0 w-32 h-32 bg-gradient-to-br from-cyan-400/10 to-blue-400/10 rounded-full blur-3xl" />
                <div className="absolute bottom-0 left-0 w-24 h-24 bg-gradient-to-br from-purple-400/10 to-pink-400/10 rounded-full blur-2xl" />
                
                <div className="relative z-10">
                  <div className="flex items-center gap-3 mb-8">
                    <Globe className="w-8 h-8 text-blue-400" />
                    <h3 className="text-2xl font-bold text-white">Contact Information</h3>
                  </div>
                  
                  <p className="text-slate-300 mb-12 leading-relaxed text-lg">
                    We are here to turn your safety challenges into real opportunities for prevention.
                  </p>
                  
                  <div className="space-y-8">
                    {[
                      { 
                        icon: <Mail size={22} />, 
                        label: 'Direct Email', 
                        value: 'vipra.prj@gmail.com',
                        color: 'from-blue-500 to-cyan-500'
                      },
                      { 
                        icon: <Phone size={22} />, 
                        label: 'Phone', 
                        value: '+39 327 0934054',
                        color: 'from-green-500 to-emerald-500'
                      },
                      { 
                        icon: <MapPin size={22} />, 
                        label: 'Project Reference', 
                        value: 'navonifederico777@gmail.com',
                        color: 'from-purple-500 to-pink-500'
                      }
                    ].map((contact, index) => (
                      <div key={index} className="group flex items-start gap-5 hover:transform hover:translate-x-2 transition-all duration-300">
                        <div className={`w-14 h-14 rounded-xl bg-gradient-to-br ${contact.color} bg-opacity-20 border border-white/10 flex items-center justify-center text-white shadow-lg group-hover:scale-110 transition-transform duration-300`}>
                          {contact.icon}
                        </div>
                        <div>
                          <div className="text-sm text-slate-400 mb-1">{contact.label}</div>
                          <div className="text-white font-semibold text-lg group-hover:text-blue-400 transition-colors duration-300">{contact.value}</div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                
                <div className="relative z-10 mt-16">
                  <div className="flex items-center gap-2 mb-6">
                    <Heart className="w-5 h-5 text-red-400" />
                    <span className="text-sm font-medium text-slate-300">Follow us</span>
                  </div>
                  <div className="flex gap-4">
                    {['LinkedIn', 'Twitter', 'Instagram', 'GitHub', 'Instagram: @vipra.prj'].map((social, index) => (
                      <a
                        key={social}
                        href="#"
                        className="w-12 h-12 rounded-xl bg-slate-800/50 border border-slate-700/50 flex items-center justify-center text-slate-300 hover:text-white hover:bg-blue-500/20 hover:border-blue-500/30 hover:scale-110 transition-all duration-300 backdrop-blur-sm"
                      >
                        <span className="sr-only">{social}</span>
                        <div className="w-5 h-5 bg-current rounded-sm opacity-80" />
                      </a>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProfessionalContact;
