import React, { useState, useEffect } from 'react';
import { BarChart3, Users, CheckCircle, Award, TrendingUp, Sparkles, Zap } from 'lucide-react';

const EnhancedProfessionalStats = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [counts, setCounts] = useState({ clients: 0, projects: 0, satisfaction: 0, years: 0 });
  const [hoveredStat, setHoveredStat] = useState(null);

  const statsData = [
    { 
      key: 'clients', 
      target: 0, 
      label: 'Clients', 
      suffix: '',
      gradient: 'from-blue-500 via-cyan-500 to-teal-400',
      icon: <Users className="w-7 h-7" />,
      description: 'Currently in prototype phase',
      pulse: true
    },
    { 
      key: 'projects', 
      target: 4, 
      label: 'Hardware Modules', 
      suffix: '',
      gradient: 'from-cyan-500 via-blue-500 to-indigo-500',
      icon: <CheckCircle className="w-7 h-7" />,
      description: 'Raspberry Pi 5, ESP32 LoRa, ESP32 PoE, Pi Camera',
      pulse: false
    },
    { 
      key: 'satisfaction', 
      target: 100, 
      label: 'Open Source', 
      suffix: '%',
      gradient: 'from-emerald-500 via-teal-500 to-cyan-500',
      icon: <Award className="w-7 h-7" />,
      description: 'All code and schematics available',
      pulse: true
    },
    { 
      key: 'years', 
      target: 1, 
      label: 'Years in Development', 
      suffix: '',
      gradient: 'from-violet-500 via-purple-500 to-indigo-500',
      icon: <TrendingUp className="w-7 h-7" />,
      description: 'Early-stage prototype',
      pulse: false
    }
  ];

  useEffect(() => {
    const timer = setTimeout(() => setIsVisible(true), 300);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    if (isVisible) {
      const duration = 2500;
      const steps = 80;
      const stepDuration = duration / steps;

      statsData.forEach(({ key, target }, index) => {
        setTimeout(() => {
          let current = 0;
          const increment = target / steps;
          
          const timer = setInterval(() => {
            current += increment;
            if (current >= target) {
              current = target;
              clearInterval(timer);
            }
            setCounts(prev => ({ ...prev, [key]: current }));
          }, stepDuration);
        }, index * 200);
      });
    }
  }, [isVisible]);

  const formatNumber = (num, stat) => {
    if (stat.key === 'satisfaction') {
      return num.toFixed(1);
    }
    return Math.floor(num).toLocaleString();
  };

  return (
    <section className="min-h-screen flex items-center justify-center py-16 px-8 relative overflow-hidden">
      {/* Dynamic Background */}
      <div className="absolute inset-0 bg-gradient-to-br from-slate-900 via-blue-900/30 to-slate-900" />
      
      {/* Floating Orbs */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(6)].map((_, i) => (
          <div
            key={i}
            className={`absolute w-64 h-64 rounded-full opacity-10 animate-pulse`}
            style={{
              background: `radial-gradient(circle, ${['#3B82F6', '#06B6D4', '#10B981'][i % 3]} 0%, transparent 70%)`,
              left: `${20 + (i * 15)}%`,
              top: `${10 + (i * 20)}%`,
              animationDelay: `${i * 0.8}s`,
              animationDuration: `${4 + i}s`
            }}
          />
        ))}
      </div>

      <div className="container mx-auto relative z-10">
        {/* Header */}
        <br />
        <div className={`text-center mb-20 transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`}>
          <div className="inline-flex items-center gap-3 bg-gradient-to-r from-cyan-500/10 to-blue-500/10 border border-cyan-500/30 px-6 py-3 rounded-full text-cyan-400 text-sm font-medium mb-8 backdrop-blur-sm">
            <BarChart3 size={20} className="animate-pulse" />
            <Sparkles size={16} className="animate-spin" style={{ animationDuration: '3s' }} />
            Measurable Results
          </div>
          
          <h2 className="text-5xl md:text-7xl font-black text-white mb-8 leading-tight">
            Excellence in
            <span className="block bg-gradient-to-r from-cyan-400 via-blue-400 to-violet-400 bg-clip-text text-transparent animate-pulse">
              Numbers
            </span>
          </h2>
          
          <p className="text-xl text-slate-300 max-w-3xl mx-auto leading-relaxed">
            Our results speak for themselves: innovation, quality, and measurable safety for every vehicle on the road.
          </p>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-8 max-w-7xl mx-auto">
          {statsData.map((stat, index) => (
            <div
              key={stat.key}
              className={`group relative transition-all duration-700 ${
                isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'
              }`}
              style={{ transitionDelay: `${index * 150}ms` }}
              onMouseEnter={() => setHoveredStat(index)}
              onMouseLeave={() => setHoveredStat(null)}
            >
              {/* Main Card */}
              <div className={`relative bg-gradient-to-br from-slate-800/60 to-slate-900/80 backdrop-blur-lg p-8 rounded-2xl border transition-all duration-500 transform ${
                hoveredStat === index 
                  ? 'border-cyan-400/60 scale-105 shadow-2xl shadow-cyan-500/20' 
                  : 'border-slate-700/50 hover:border-cyan-500/40'
              }`}>
                
                {/* Glow Effect */}
                <div className={`absolute inset-0 bg-gradient-to-br ${stat.gradient} opacity-0 ${hoveredStat === index ? 'opacity-10' : 'group-hover:opacity-5'} rounded-2xl transition-opacity duration-500`} />
                
                {/* Pulse Animation for Special Stats */}
                {stat.pulse && hoveredStat === index && (
                  <div className="absolute inset-0 rounded-2xl border-2 border-cyan-400/30 animate-ping" />
                )}
                
                {/* Icon */}
                <div className={`relative mb-6 inline-flex items-center justify-center w-16 h-16 rounded-xl bg-gradient-to-br ${stat.gradient} shadow-lg transform transition-transform duration-300 ${hoveredStat === index ? 'scale-110 rotate-3' : 'group-hover:scale-105'}`}>
                  <span className="text-white relative z-10">
                    {stat.icon}
                  </span>
                  {hoveredStat === index && (
                    <Zap className="absolute -top-1 -right-1 w-4 h-4 text-yellow-400 animate-bounce" />
                  )}
                </div>

                {/* Number Display */}
                <div className="relative z-10 mb-4">
                  <div className={`text-5xl font-black text-white mb-2 font-mono transition-colors duration-300 ${hoveredStat === index ? 'text-cyan-300' : ''}`}>
                    {formatNumber(counts[stat.key], stat)}
                    <span className="text-cyan-400 ml-1">{stat.suffix}</span>
                  </div>
                  <div className="text-lg font-semibold text-slate-200 mb-2">
                    {stat.label}
                  </div>
                  <div className={`text-sm text-slate-400 transition-all duration-300 ${hoveredStat === index ? 'text-slate-300' : ''}`}>
                    {stat.description}
                  </div>
                </div>
                
                {/* Progress Bar */}
                <div className="relative">
                  <div className="h-2 bg-slate-700/50 rounded-full overflow-hidden">
                    <div 
                      className={`h-full bg-gradient-to-r ${stat.gradient} transition-all duration-1000 ease-out relative`}
                      style={{ 
                        width: `${isVisible ? '100%' : '0%'}`,
                        transitionDelay: `${index * 200 + 500}ms`
                      }}
                    >
                      <div className="absolute inset-0 bg-white/20 animate-pulse" />
                    </div>
                  </div>
                  
                  {/* Achievement Badge */}
                  {hoveredStat === index && (
                    <div className="absolute -top-8 right-0 bg-gradient-to-r from-yellow-400 to-orange-400 text-black text-xs font-bold px-3 py-1 rounded-full animate-bounce">
                      TOP RATED
                    </div>
                  )}
                </div>

                {/* Decorative Elements */}
                <div className="absolute top-4 right-4 opacity-20 group-hover:opacity-40 transition-opacity duration-300">
                  <div className="w-8 h-8 border-2 border-cyan-400 rounded-full animate-spin" style={{ animationDuration: '4s' }} />
                </div>
              </div>

              {/* Floating Number Effect */}
              {hoveredStat === index && (
                <div className="absolute -top-4 left-1/2 transform -translate-x-1/2 text-cyan-400 font-bold text-lg animate-bounce pointer-events-none">
                  #{index + 1}
                </div>
              )}
            </div>
          ))}
        </div>

        {/* Bottom Achievement Bar */}
        <div className={`mt-16 text-center transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-10'}`} style={{ transitionDelay: '800ms' }}>
          <div className="inline-flex items-center gap-4 bg-gradient-to-r from-emerald-500/10 to-cyan-500/10 border border-emerald-500/30 px-8 py-4 rounded-full backdrop-blur-sm">
            <Award className="w-6 h-6 text-emerald-400" />
            <span className="text-emerald-400 font-semibold">ISO 27001  SOC 2 Type II  GDPR Compliant</span>
            <Sparkles className="w-6 h-6 text-emerald-400 animate-pulse" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default EnhancedProfessionalStats;